import deliveryIcon from "../assets/images/delivery.gif";
import scaleIcon from "../assets/images/scale.gif";
import timeIcon from "../assets/images/time.gif";

const Second = () => {
  return (
    <section id="second">
      <div className="wrapper">
        <div className="benefits-container">
          <div className="benefit-item">
            <div className="img drawing" style={{ backgroundImage: `url(${scaleIcon})` }}></div>
            <p>Изделие по вашему чертежу</p>
            <span>Какое-то описание</span>
          </div>
          <div className="benefit-item">
            {/* <div className="img" style={{ backgroundImage: `url(${timeIcen})` }}></div> */}
            <img src={timeIcon} alt="" />
            <p>Изготовление 1 день</p>
            <span>Какое-то описание</span>
          </div>
          <div className="benefit-item">
            {/* <div className="img" style={{ backgroundImage: `url(${deliveryItem})` }}></div> */}
            <img src={deliveryIcon} alt="" />
            <p>Бесплатная доставка по городу</p>
            <span>Комплексные доставки в ДНР и ЛНР</span>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Second;
